<template>
	<div>
		<Breadcrumb class="bread">
			<div class="title">我的期权奖励</div>
			<Button type="primary" size="large" icon="md-barcode" @click="myPlanList">返回</Button>
		</Breadcrumb>
		<div>
			<Row>
				<Col span="24">
					<Card :bordered="false" class="capTable">
						<p slot="title" class="tipMessage">期权奖励列表</p>
						<div class="tabBoard">
							<Table  :loading="loading" height="700" :border="true" :stripe="true" :columns="planTabList.rows" :data="planTabList.cols">
								 <template slot-scope="{ row, index }" slot="types">
						            <Button class="tabBtn" v-if="row.types == undefined" type="primary" size="small" @click="isAddDrawer = !isAddDrawer">签名</Button>
						            <span v-else>{{row.types}}</span>
						        </template>
							</Table>
						</div>
					</Card>
				</Col>
			</Row>
		</div>
		 <Modal
			v-model="helpBox"
			title="期权机制描述"
			class-name="vertical-center-modal">
			<p>本公司计划于<b>【创建日期】</b>创建名为<b>【机制名称】</b>的期权激励计划（ESOP），该ESOP将预留共计<b>【股权量】</b>QT，约占公司股权的 * 作为期权池。</p>
			<p>该期权激励机制下，员工将会在第一个到期日被授予<b>【首次授权量】</b>的股份，其余75%的股份须于首个到期日期起按<b>【时间单位】</b> 授予，总计<b>【周期】</b> 个月。</p>
			
			<div slot="footer" class="tipMsg">
				<p>期权发行条款:</p>
				<p>期权严格按照既定计划发放。</p>
				<p>如果员工不再受雇于公司,任何未成熟期权将自动失效，并放回公司期权池。</p>
			</div>
		</Modal>
		<Drawer title="执行通知" width="400" :closable="false" v-model="isAddDrawer" class="newVoteBoard">
			<div>
				<h3>期权执行通知</h3>
				<h4>LittlebeeX Pte. Ltd </h4>
				<p>
					1.根据贵方于2019年1月16日授予的授权书，本人在此对在LittlebeeX有限公司的股本中按照1美元的执行价格，认购25 个公司股权Token，总计25美元。
				</p>
				<p>
					2.我希望通过电汇的方式支付1美元的执行价价，并向公司提供电汇的确认
				</p>
				<p>
					3.本人谨此声明，本人为本人认购上述普通股，而非以他人名义认购。
				</p>
				<p>
					4.本人承认本人将继续遵守《普通员工期权计划》及《股东协议》中有关该期权的规定。
				</p>
				<p>名字: <b>爱丽丝</b></p>
				<div class="btn">
					<Button type="success" long >{{$t('homeVote.btn5')}}</Button>
				</div>
			</div>
		</Drawer>
	</div>
</template>

<script>
	import Qs from 'qs'
	import {mapState} from 'vuex'
	import mutil from '@/util/mutil'
	export default {
		data(){
			return{
				helpBox:false,
				thisPlan:'1',
				loading: true,
				isAddDrawer:false,
				planList:[
					{
						value:'1',
						label:'期权计划1'
					},
					{
						value:'2',
						label:'期权计划2'
					},
					{
						value:'3',
						label:'期权计划3'
					},
					{
						value:'4',
						label:'期权计划4'
					}
				],
				planTabList:{
					rows:[
						{
							title: '到期日',
							key: 'overDate',
							align:'center'
						},
						{
							title: '执行周期',
							key: 'cycle',
							align:'center'
						},
						{
							title: '数量',
							key: 'num',
							align:'center'
						},
						{
							title: 'TOKEN市价(USD)',
							key: 'getNum',
							align:'center'
						},
						{
							title: 'TOKEN执行价格(USD)',
							key: 'endNum',
							align:'center'
						},
						{
							title: '状态',
							key: 'types',
							slot: 'types',
							align:'center'
						}
					],
					cols:[
						{
							cycle:'首个期权执行日',
							num:25,
							getNum:1.5,
							endNum:1,
							overDate:'2018-12-12'
						},
						{
							cycle:'月执行日',
							num:2,
							getNum:1.6,
							endNum:1,
							overDate:'2018-12-12',
							types:'等待'
						},
						{
							cycle:'月执行日',
							num:2,
							getNum:1.6,
							endNum:1,
							overDate:'2018-12-12',
							types:'等待'
						},
						{
							cycle:'月执行日',
							num:2,
							getNum:1.6,
							endNum:1,
							overDate:'2018-12-12',
							types:'等待'
						},
						{
							cycle:'月执行日',
							num:2,
							getNum:1.6,
							endNum:1,
							overDate:'2018-12-12',
							types:'等待'
						},
						{
							cycle:'月执行日',
							num:2,
							getNum:1.6,
							endNum:1,
							overDate:'2018-12-12',
							types:'等待'
						}
					]
				}
			}
		},
		computed: {
			...mapState({
				Address: state => state.web3.coinbase
			})
		},	
		methods:{
			myPlanList(){
				this.$router.push({
					path:'myPlan',
					query:{
						"only":this.$route.query.only
					}
				})
			},
			refreshPlanList(){
				let data = {
					"only": this.$route.query.only,
					"address":this.Address,
					"option":this.$route.query.planId
				};
				this.$axios({
					method: 'post',
					url: '/index.php/cn/home//node_se/plan',
					data: Qs.stringify(data)
				}).then((response) => {
					if(response.data.state == 0){
						let planListDetail = response.data.info
						this.planList = []
						if(planListDetail.length > 0){
							for(let i=0;i<planListDetail.length;i++){
								this.planList.push({
									id:planListDetail[i].id,
									label:planListDetail[i].name
								})
							}
						}
					}
				})
			}
		},
		created(){
			this.$emit('menuActiveName', "14-3")
			this.$emit('menuOpenNames', "14")
			this.loading = false
			this.refreshPlanList()
		}
	}
</script>
<style>
	.warningProgress .ivu-progress-bg{
		background-color: #ed4014
	}
	.tooltip-msg{
		width: 300px	
	}
	.ivu-tooltip{width: 100%;}
	.newVoteBoard .btn span{
		font-size:14px
	}
</style>
<style scoped lang="stylus">
	flexs()
		display:flex
		align-items: center
		justify-content: space-between
		
	.bread
		flexs()
	
	.tabBtn
		padding: 5px 15px
	
	.newVoteBoard
		h3,h4
			text-align: center
		h3
			font-size:16px
		h4
			font-size:14px
			font-weight: 400
			margin-bottom: 30px
		p
			line-height:22px
			margin-bottom: 15px
			font-size:14px
		.btn
			margin-top: 50px
			span
				font-size: 14px	
</style>





